#infiniteoptions-container {
  margin-bottom: 30px;

  div[role="group"]>span {
    display: flex;
    flex-wrap: wrap;
  }

  .spb-label-tooltip>label {
    margin-bottom: 20px;
    letter-spacing: 0.3px;
    font-weight: 600;
    font-size: 12px;
  }

  .spb-productoptionswatchwrapper {
    display: flex;
    text-align: center;
    flex-direction: column;
    align-items: center;
    padding-right: 8px;
    padding-left: 8px;
    margin-right: 3px;
    margin-left: 1px;
    max-width: 45px;

    font-family: "Open Sans";
    font-size: 9px;
    font-weight: 400;
    letter-spacing: 0.01px;
    line-height: 12px;

    position: relative;
    max-height: 64px;
    padding-top: 37px;

    input[type="radio"]:checked+.spb-productoptionswatch {
      border: 1px solid #000;
    }

    &:hover,
    &:focus {
      color: #403d3e;
    }

    &>span {
      margin-bottom: 7px;
    }
  }

  .spb-productoptionswatch {
    position: absolute;
    margin-right: 0;
    top: 0;
  }
}
