/// Simple, elegant and maintainable media queries in Sass.
/// @see https://include-media.com/
@import "../vendors/include-media/dist/include-media";
@import "apps/_infinate-options.scss";
@import "components/_hero-banner.scss";
@import "global/_hero.scss";


//variables

$text-dark: #202a36;
$price-red: #c22429;
$productDetails-red: #ef2e54;
$color-dragon-skin: purple;
$color-fire-rune: red;
$color-soul-reaper: blue;
$color-purifying-code: #c4c4c4;
$color-fel-magic: lime;
$color-lich-blade: #00acd8;
$color-scorpion-chain: gold;
$color-twilight-princess: #ee1ea1;
$color-portal-jump: orange;

//general

.index-section{
    margin: 40px 0;
}

.rte{
    p{
        font-size: 16px;
    }
}

.h3{
    color: $text-dark;
    font-size: 33px;
}

.h5{
    font-size: 14px;
    letter-spacing: 4px;
}

.section-header__title, .section-header h2{
    font-size: 33px;
}

.title-colored{
    color: #ef2e54;
}

.breadcrumb{
    font-size: 14px;

    .breadcrumb-current{
        color: #ef2e54;
    }
}

.spr-starrating.spr-badge-starrating{
    .spr-icon.spr-icon-star{
        font-size: 14px;
    }
}

.btn, .shopify-payment-button__button{
    border-radius: 5px !important;
}

.hero__link, .text-columns__button-wrapper, .feature-row__item, .video-parent-section{
    .btn{
        border-radius: 0 !important;
    }
}



//announcement bar

.announcement-bar{
    padding: 5px 0;
}

#AnnouncementSlider{

    .announcement-text{
        font-size: 13px;
    }

    .announcement-link-text{
        font-size: 13px;
        font-weight: 600;
    }

}

//header

#shopify-section-header{

    .site-header{
        padding: 0 0 20px 0;
    }

    .toolbar__content{
        .toolbar__text{
            margin-right: auto;
            font-size: 12px;
            font-weight: 600;
        }
    }

    .site-nav{
        &__item{
            a{
                font-size: 16px;
                font-weight: 600;
            }
        }

        &__icons{
            svg{
                width: 22px;
                height: 22px;
            }
        }
    }

}

//hero

.index-section--hero{

    *{
        text-align: left;
    }

    .h1.hero__title{
        letter-spacing: 2px;
    }

    .hero__subtitle{
        font-size: 18px;
    }

    .hero__link{
        .btn{
            color: #FFF !important;
        }
    }

}

//featured content section

[data-section-type=featured-content-section]{
    .rte{
        p{
            font-size: 18px !important;
        }
    }
    .featured-content{

        &__subtitle{
            text-transform: uppercase;
            font-size: 14px;
            color: #8b8b8b;
            letter-spacing: 4px;
            line-height: 14px;
        }

        &__title{

            font-size: 33px;
            color: $text-dark;

        }

    }
}

//text columns with images

.grid.grid--uniform.grid--flush-bottom{
    display: flex;

    h3{
        font-size: 18px;
        color: $text-dark;
        margin-bottom: 5px;
        font-weight: 700;
    }

    .rte-setting.text-spacing{
        p{
            line-height: 21px;
            width: 70%;
            margin: 0 auto;
            font-size: 1.1em;
        }
    }
}

.text-columns__button-wrapper{
    width:100%;
    display:flex;
    margin-top: 30px;

    a{
        margin: 0 auto;
    }
}

//feature row section



//text and image section

.page-width.full-width{
    margin: 0;
    padding: 40px 0;
    max-width: 100%;

    .feature-row{
        max-width: 1500px;
        margin: 0 auto;
        padding: 0 40px;
    }

    .detail-rows__wrapper{

        display: flex;
        flex-direction: column;

        .detail-row{

            display: flex;
            flex-direction: row;
            padding: 20px;

            &__image{
                flex-basis: 10%;

                img{
                    max-width: 60px;
                }
            }

            &__text{
                flex-basis: 90%;
                display: flex;
                flex-direction: column;
                margin-left: 10px;

                .row-heading{
                    font-size: 16px;
                    font-weight: 700;
                    line-height: 24px;
                    color: $text-dark;
                }

                .row-mainText{
                    width: 70%;
                }
            }
        }
    }

    .detail-iconsOnly__wrapper{
        display: flex;
        flex-direction: row;
        width: 75%;
        margin-top: 30px;

        .detail-icon{
            flex-basis: 25%;

            &:not(:last-child){
                border-right: 1px solid #c7c7c7;
            }

            img{
                width: auto;
                height: 60px;
                margin: 0 auto;
            }

            .detail-icon__heading{
                width: 100%;
                text-align: center;
                font-size: 14px;
                font-weight: 600;
                color: $text-dark;
                margin-top: 10px;
            }
        }
    }
}

//product section

.product__photos.product__photos--beside{
    flex-direction: row-reverse;
}

.product__price{
    color: $price-red;
    font-size: 22px;
    font-weight: 600;
}

.product-single__meta{
    hr.hr--medium{
        margin: 15px auto;
    }
    .product-single__title{
        color: $text-dark;
    }
}

.add-to-cart.btn--secondary {
    border-color: $productDetails-red;
}

.payment-buttons{

    button{

        border-color: $productDetails-red;
        letter-spacing: 1.43px;
        background-color: #FFF;

        span{
            color: $productDetails-red;
            font-weight: 700;
        }

    }
}

//collection section

[data-section-type=collection-template]{
    @include media('>768px') {
        padding: 40px 0;
    }

    .section-header{
        margin-bottom: 30px;
    }

    .section-header__title{
        color: $text-dark;
    }

    .grid-product__title{
        font-size: 18px;
        color: $text-dark
    }

    .grid-product__price{
        font-weight: 700;
        font-size: 15px;
        color: $price-red;
    }
}

//testimonials section

[data-section-type=testimonials]{
    .section-header{
        h2{
            color: $text-dark;
        }
    }

    .testimonials-location{
        font-size: 14px;
        font-weight: 700;
        color: #636364;
    }
}

.testimonials-wrapper{

    .testimonials-slider__text{

        padding: 0 50px;

        .quote-icon{
            position: absolute;
            color: rgba(239, 46, 84, 0.1);

            .icon-quote{
                width: 40px;
                height: 40px;
            }

        }

        .rte-setting{
            p{
                font-size: 14px;
            }
        }
        .name-and-stars{
            display: inline-flex;
            width: 100%;
            cite{
                font-size: 16px;
                font-weight: 700;
                color: $productDetails-red;
                flex-grow: 1;
                text-align: left;

                &:before{
                    display: none;
                }
            }
        }

        .testimonials-location{
            text-align: left;
        }
    }
}

//section feed

.ooo-instagram{
    margin-bottom: 0 !important;
    .ooo-instagram-header{
        margin-bottom: 0 !important;
        padding: 0 !important;
        h2.h2{
            margin-bottom: 0 !important;
            padding: 20px 0;
            background: #f4f4f4;
        }
    }
    .ooo-instagram-feed__list{
        padding: 0 !important;
    }
}

//footer

.site-footer{
    .h4{
        font-size: 14px;
        font-weight: 700;
        letter-spacing: 2px;

    }

    .footer__collapsible{
        p{
            font-size: 14px;
            line-height: 21px;
        }
    }

    ul{
        li{
            a{
                font-size: 13px;
                letter-spacing: 0.5px;
            }
        }
    }

    .footer__newsletter{
        input{
            font-size: 14px;
        }
    }
}

.page-width{
    &.footer-promotions{
        .grid__item{
            padding-left: 0;
        }
    }
}

//collection page

#shopify-section-collection-header{
    .section-header__title{
        color: #FFF;
        font-size: 55px;
    }
}

.collection-filter__item--count{
    font-size: 14px;
    color: $text-dark;
}

.collection-filter__item--sort{
    min-width: 200px;
    flex: 0 1 25%;
    @include media("<=450px") {
        flex: 1;
        padding-left: 0;
    }
}

.collection-filter-button-wrapper{
    // margin-left: -20px;
    margin-right: -20px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .collection-filter-button{
        margin: 0 20px 20px 0;
        padding: 10px;
        flex-grow: 1;
        text-align: center;
        font-size: 16px;
        text-transform: uppercase;
        font-weight: 600;
        border: 1px solid #c7c7c7;
        color: #202a36;
        max-width: 600px;
        border-radius: 5px;

        &.current-filter{
            background-color: #ef2e54;
        }
    }
}

#SortBy{
    font-size: 14px;
    color: $text-dark;
}

//product page

.product-section{
    background-color: #f4f4f4;

    .product__photos{
        flex-direction: row;
    }

    .js-qty__wrapper{
        background-color: #f4f4f4;

        .js-qty__adjust--minus{
            border-right-color: #e8e8e1;
        }

        .js-qty__adjust--plus{
            border-left-color: #e8e8e1;
        }
    }

    .product__thumb-item{
        &.slick-current.slick-active{
            border-color: #ef2e54;
        }
    }
}


.product-faqs-section{
    background-color: #f4f4f4;
    padding: 40px 0;

    .page-width{
        display: flex;
        flex-direction: row;

        div{
            flex-basis: 50%;

            .faqs-title{
                font-size: 33px;
                font-weight: 600;
                color: #202a36;
            }

            .faq-question{
                background: #FFF;
                padding: 20px;
                margin: 10px 0;

                .faq-q{
                    font-size: 16px;
                    font-weight: 600;
                    color: #202a36;
                    cursor: pointer;

                    .faq-icon{
                        float: right;

                        &__minus{
                            display: none;
                        }

                        &__plus{
                            display: block;
                        }
                    }
                }

                &.question-open{
                    .faq-icon{
                        &__minus{
                            display: block;
                        }

                        &__plus{
                            display: none;
                        }
                    }

                    .faq-a{
                        display: block;
                    }
                }

                .faq-a{
                    font-size: 14px;
                    margin-top: 20px;
                    display: none;
                }
            }
        }
    }
}

#shopify-section-product-story-sections, #shopify-section-footstool-story-sections{
    .feature-row{
        margin-top: 40px;
        .feature-row-item-wrapper{
            display: inline-flex;
        }
    }
    .faqs-image{
        padding-top: 5.7%;
        text-align: center;
        img{
            height: 100%;
            max-height: 580px;
        }
    }
}

#shopify-product-reviews{
    .spr-header-title{
        font-size: 33px !important;
        color: $text-dark;
        margin-top: 40px;
    }

    .spr-summary-caption{
        font-size: 14px;
    }

    .spr-summary-actions-newreview{
        letter-spacing: 1.43px;
    }
}

#shopify-section-product-recommendations{
    .section-header__title{
        color: $text-dark;
    }
    .grid-product__title{
        &--body{
            font-weight: 700;
        }
    }
}

//colour swatches

.jr-swatches{
    ul{
        margin: 0;
        margin-bottom: 15px;
        text-align: left;
        padding: 0;
        margin-left: -8px;
        display: flex;

        @include media("<=768px") {
            display: flex;
            justify-content: center;
        }

        li{
            display: inline-block;
            margin: 4px;
            border: 2px solid #c7c7c7;
            border-radius: 100%;
            position: relative;

            a{
                position: relative;
                display: block;
                border-radius: 100%;
                padding: 3px;
                margin: auto;
            }

            span {
                display: block;
                width: 25px;
                height: 25px;
                border-radius: 100%;
            }

            &.sold-out:after{
                position: absolute;
                left: 0;
                top: 50%;
                height: 2px;
                background: #000;
                content: "";
                width: 100%;
                transform: rotate(-45deg);
            }

            &.jr-active-swatch{
                border-color: #ef2e54;
            }

            &.colour-dragon-skin{
                span{
                    background: $color-dragon-skin;
                }
            }

            &.colour-fire-rune{
                span{
                    background: $color-fire-rune;
                }
            }

            &.colour-soul-reaper{
                span{
                    background: $color-soul-reaper;
                }
            }

            &.colour-purifying-code{
                span{
                    background: $color-purifying-code;
                }
            }

            &.colour-fel-magic{
                span{
                    background: $color-fel-magic;
                }
            }

            &.colour-lich-blade{
                span{
                    background: $color-lich-blade;
                }
            }

            &.colour-scorpion-chain{
                span{
                    background: $color-scorpion-chain;
                }
            }

            &.colour-twilight-princess{
                span{
                    background: $color-twilight-princess;
                }
            }

            &.colour-portal-jump{
                span{
                    background: $color-portal-jump;
                }
            }

            &.colour-red {
                span{
                    background: #c22429;
                    background-image: url(#{'{{ "colour-red.png" | assets_url }}'});
                    background-size: contain;
                }
            }
            &.colour-blue {
                span{
                    background: #2B446E;
                    background-image: url(#{'{{ "colour-blue.png" | assets_url }}'});
                    background-size: contain;
                }
            }
            &.colour-black {
                span{
                    background: black;
                    background-image: url(#{'{{ "colour-black.png" | assets_url }}'});
                    background-size: contain;
                }
            }
            &.colour-grey {
                span{
                    background: grey;
                    background-image: url(#{'{{ "colour-grey.png" | assets_url }}'});
                    background-size: contain;
                }
            }

        }
    }
}

//FAQs page

.faqs-page{
    .questions-wrapper{
        h2{
            margin-top: 40px;
        }
    }
    .page-width{
        .faqs-image{
            margin-right: 40px;
            margin-top: 8.7%;
            img{
                height: auto;
                width: 100%;
            }
        }
    }
}

//media queries

@media only screen and (max-width: 600px){
    .grid.grid--uniform.grid--flush-bottom{
        display: block;
    }

    .page-width.full-width{
        .detail-rows__wrapper{
            .detail-row{
                .detail-row{

                    &__image{
                        flex-basis: 25%
                    }

                    &__text{
                        flex-basis: 75%;
                        margin-left: 20px;

                        .row-mainText, .row-heading{
                            width: 100%;
                            text-align: left;
                        }
                    }
                }
            }
        }

        .detail-iconsOnly__wrapper{
            width: 100%;

            .detail-icon{

                padding: 5px;

                img{
                    height: 40px;
                }

                .detail-icon__heading{
                    font-size: 12px;
                }
            }
        }
    }

    .hero__subtitle{
        .animation-contents{
            font-size: 12px;
        }
    }

    .product-faqs-section{
        .page-width{
            div{
                .faq-question{
                    .faq-q{
                        font-size: 16px;
                        display: inline-flex;
                        width: 100%;

                        &-text{
                            flex-basis: 85%;
                        }

                        .faq-icon{
                            flex-basis: 15%;
                        }
                    }
                }
            }
            .faqs-image{
                img{
                    width: 100%;
                    height: auto;
                }
            }
        }
    }

    #shopify-section-product-story-sections, #shopify-section-footstool-story-sections{
        .feature-row{
            .feature-row-item-wrapper{
                display: block;
            }
        }
    }

    .jr-swatches ul li span{
        width: 20px;
        height: 20px;
    }
}

@media only screen and (max-width: 1500px){
    .product-faqs-section{
        .page-width{
            flex-direction: column;

            .faqs{
                &-image, &-content{
                    flex-basis: 100%;
                    text-align: center;
                    margin: 20px 0;
                }
            }
        }
    }
    .faqs-page{
        .faqs-image{
            display: none;
        }
    }
}
