.jr-swatches ul {
  margin: 0;
  margin-bottom: 15px;
  text-align: left;
  padding: 0;
  margin-left: -8px;
  display: flex;
}

@media (max-width: 768px) {
  .jr-swatches ul {
    display: flex;
    justify-content: center;
  }
}

.jr-swatches ul li {
  display: inline-block;
  margin: 4px;
  border: 2px solid #c7c7c7;
  border-radius: 100%;
  position: relative;
}

.jr-swatches ul li a {
  position: relative;
  display: block;
  border-radius: 100%;
  padding: 3px;
  margin: auto;
}

.jr-swatches ul li span {
  display: block;
  width: 25px;
  height: 25px;
  border-radius: 100%;
}

.jr-swatches ul li.sold-out:after {
  position: absolute;
  left: 0;
  top: 50%;
  height: 2px;
  background: #000;
  content: "";
  width: 100%;
  transform: rotate(-45deg);
}

.jr-swatches ul li.jr-active-swatch {
  border-color: #ef2e54;
}

.jr-swatches ul li.colour-dragon-skin span {
  background: purple;
}

.jr-swatches ul li.colour-fire-rune span {
  background: red;
}

.jr-swatches ul li.colour-soul-reaper span {
  background: blue;
}

.jr-swatches ul li.colour-purifying-code span {
  background: #c4c4c4;
}

.jr-swatches ul li.colour-fel-magic span {
  background: lime;
}

.jr-swatches ul li.colour-lich-blade span {
  background: #00acd8;
}

.jr-swatches ul li.colour-scorpion-chain span {
  background: gold;
}

.jr-swatches ul li.colour-twilight-princess span {
  background: #ee1ea1;
}

.jr-swatches ul li.colour-portal-jump span {
  background: orange;
}

.jr-swatches ul li.colour-red span {
  background: #c22429;
  background-image: url(/*!{{*/ "colour-red.png" | assets_url /*!}}*/);
  background-size: contain;
}

.jr-swatches ul li.colour-blue span {
  background: #2B446E;
  background-image: url(/*!{{*/ "colour-blue.png" | assets_url /*!}}*/);
  background-size: contain;
}

.jr-swatches ul li.colour-black span {
  background: black;
  background-image: url(/*!{{*/ "colour-black.png" | assets_url /*!}}*/);
  background-size: contain;
}

.jr-swatches ul li.colour-grey span {
  background: grey;
  background-image: url(/*!{{*/ "colour-grey.png" | assets_url /*!}}*/);
  background-size: contain;
}

